import React from "react";
import { Container, Row, Card } from "react-bootstrap";
import hospitalized from "./assets/hospitalized.png";
import infected from "./assets/infected.png";
import recovered from "./assets/recovered.png";
import deaths from "./assets/deaths.png";
import "./App.css";
import "./components/Nav.js";
import "bootstrap/dist/css/bootstrap.min.css";

class App extends React.Component {
  constructor() {
    super();

    this.state = {
      covidData: null,
    };
  }

  componentDidMount() {
    fetch("https://hpb.health.gov.lk/api/get-current-statistical")
      .then((Response) => Response.json())
      .then((users) => {
        console.log(users);
        this.setState({
          covidData: users.data,
        });
      });
  }

  render() {
    return (
      <div className="App">
        {this.state.covidData != null && (
          <Container>
            <h2>test cases</h2>
            <h2>{this.state.covidData.local_total_cases}</h2>

            <Row>
              <Card style={{ width: "250px" }}>
                <Card.Img variant="top" src={infected} />
                <Card.Body>
                  <Card.Title>Total Infected</Card.Title>
                  <h2>{this.state.covidData.local_total_cases}</h2>
                </Card.Body>
              </Card>

              <Card style={{ width: "250px" }}>
                <Card.Img variant="top" src={hospitalized} />
                <Card.Body>
                  <Card.Title>Hospitalized</Card.Title>
                  <h2>{this.state.covidData.local_new_cases}</h2>
                </Card.Body>
              </Card>

              <Card style={{ width: "250px" }}>
                <Card.Img variant="top" src={deaths} />
                <Card.Body>
                  <Card.Title>Deaths</Card.Title>
                  <h2>{this.state.covidData.local_deaths}</h2>
                </Card.Body>
              </Card>

              <Card style={{ width: "250px" }}>
                <Card.Img variant="top" src={recovered} />
                <Card.Body>
                  <Card.Title>Recovered</Card.Title>
                  <h2>{this.state.covidData.local_recovered}</h2>
                </Card.Body>
              </Card>
            </Row>
            <footer>
              Made with ‚ù§ by LakinduK | Powered by hpb.health.gov.lk
            </footer>
          </Container>
        )}
      </div>
    );
  }
}

export default App;
